import { Loadmore } from "mint-ui";
import * as utils from '../../utils'  
import * as api from './api'  
export default {
  data() {
    return {
      addRecords: [
        {
          AccountName: "中国电信*****缴费部",
          Doorno: "88888888",
          Address: "浙江省绍兴市成州市三街道阮庙*******尹村"
        }
      ],
      RecordList: [],
      list: [
         {
          Paymentmethods: "电易宝代收(常收)",
          price: "20.00",
          Accountame: "栋梁",
          Doorno: "88888888",
          Datetime: "2019-09-10 12:12:12"
        },
        {
          Paymentmethods: "电易宝代收(常收)",
          price: "23.00",
          Accountame: "栋梁",
          Doorno: "88888888",
          Datetime: "2019-10-10 12:12:12"
        },
        {
          Paymentmethods: "电易宝代收(常收)",
          price: "89.00",
          Accountame: "栋梁",
          Doorno: "88888888",
          Datetime: "2019-11-10 12:12:12"
        },
        {
          Paymentmethods: "电易宝代收(常收)",
          price: "9.90",
          Accountame: "栋梁",
          Doorno: "88888888",
          Datetime: "2019-12-10 12:12:12"
        },
        {
          Paymentmethods: "电易宝代收(常收)",
          price: "7.80",
          Accountame: "栋梁",
          Doorno: "88888888",
          Datetime: "2020-09-10 12:12:12"
        }
      ],

      allLoaded: false, //如果true，bottomMethod将无法再触发
      topStatus: "",
      bottomStatus: "",
      wrapperHeight: 0,
      pageNum: 1, //当前页
      pageSize: 10, //列表数,
      bpText: "正在加载",
      bdText: "暂无数据",
      blText: "加载更多",
      showTop:true,
      categoryId:""
    };
  },
  created() {},
  mounted() {
    document.title = "电费情况";//标题设置
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().top; //下拉加载容器高度
    this.wrapperHeight =
      document.documentElement.clientHeight -
      this.$refs.wrapper.getBoundingClientRect().bottom; //上拉加载容器高度
    
    this.getList(); //页面初始化数据
    this.loadTop();
    this.categoryId = this.$route.query.tabIndex
    console.log(this.categoryId)
  },
  components: {
    "mt-loadmore": Loadmore //组件引入
  },
  methods: {
    handleTopChange(status) {
      this.topStatus = status;
    },
    loadTop() {
      this.$refs.loadmore.onTopLoaded();
      setTimeout(() => {
        this.RecordList = this.list;
        this.$refs.loadmore.onTopLoaded();
      }, 1000);
    },
    handleBottomChange(status) {
      this.bottomStatus = status;
    },
    loadBottom() {
      this.handleBottomChange("pull"); //上拉时改变状态码
      this.pageNum += 1;;
      setTimeout(() => {
        if (this.pageNum <= 3) {
          this.RecordList=this.RecordList.concat(this.list);
        } else {
          this.allLoaded = true; //模拟数据加载完毕 禁用上拉加载
        }
        this.handleBottomChange("drop"); //数据加载完毕 修改状态码
        this.$refs.loadmore.onBottomLoaded();
      }, 1000);
      this.getList()
    },
    //发送请求
    async getList() {
      let params = {
        pageNumber: 0,
        pageSize: 10 //每次请求10条数据
      };
      const res = await utils.HTTP(`${api.paymentRecords}`, params);
      console.log(res)
      if(res.code == 1) {
        this.RecordList=res.data
        this.pageSize++;
      }
    }
  }
};



